import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import axios from "axios";
import NotFound from "@/ticketing/views/NotFound";
import VueMeta from "vue-meta";

import store from "@/store/store";
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
Vue.use(Router);
Vue.use(VueMeta);
const router = new Router({
  // mode: "history",
  // base: "/events/05-2019/ticketing/",
  routes: [
    {
      path: "/:eventSlug/rsvp/closed",
      name: "rsvps-closed",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPClosed"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getEvent(routeTo.params.eventSlug)
          .then(res => {
            routeTo.params.event = res.data.event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/rsvp/:personId",
      name: "rsvp",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVP"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            ProfileService.getProfile(routeTo.params.personId),
            TicketService.getEvent(routeTo.params.eventSlug)
          ])
          .then(
            axios.spread((personRes, eventRes) => {
              const event = eventRes.data.event;
              store.dispatch("layouts/setLayout", event.layout);
              const person = personRes.data.person;
              if (!event.hasRsvps) {
                next({
                  name: "404",
                  params: { message: "This event doesn't have RSVP's enabled." }
                });
              }
              if (!event.rsvpsActive) {
                next({
                  name: "rsvps-closed",
                  params: { eventSlug: event.slug }
                });
              }
              routeTo.params.event = event;
              routeTo.params.person = person;
              if (person.eventRSVPIds.includes(event.id)) {
                TicketService.getRSVP(event.slug, person.id).then(res => {
                  routeTo.params.rsvp = res.data.rsvp;
                  next();
                });
              } else {
                TicketService.getNewRSVP(event.slug).then(res => {
                  routeTo.params.rsvp = res.data.rsvp;
                  next();
                });
              }
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/rsvp",
      name: "rsvp-new-person",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVP"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            ProfileService.getNewProfile(),
            TicketService.getEvent(routeTo.params.eventSlug)
          ])
          .then(
            axios.spread((personRes, eventRes) => {
              const event = eventRes.data.event;
              store.dispatch("layouts/setLayout", event.layout);
              const person = personRes.data.person;
              if (!event.hasRsvps) {
                next({
                  name: "404",
                  params: { message: "This event doesn't have RSVP's enabled." }
                });
              }
              if (!event.rsvpsActive) {
                next({
                  name: "rsvps-closed",
                  params: { eventSlug: event.slug }
                });
              }
              routeTo.params.event = event;
              routeTo.params.person = person;
              TicketService.getNewRSVP(event.slug).then(res => {
                routeTo.params.rsvp = res.data.rsvp;
                next();
              });
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply",
      name: "apply",
      component: () =>
        import(/* webpackChunkName: "apply" */ "@/ticketing/views/Apply"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getEvent(routeTo.params.eventSlug)
          .then(response => {
            const event = response.data.event;
            store.dispatch("layouts/setLayout", event.layout);

            if (!event.hasTickets) {
              next({
                name: "404",
                params: { message: "This event doesn't have Tickets enabled." }
              });
            }
            if (!event.applicationsActive) {
              next({
                name: "applications-closed",
                params: { eventSlug: routeTo.params.eventSlug }
              });
            }
            routeTo.params.event = event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/closed",
      name: "applications-closed",
      component: () =>
        import(/* webpackChunkName: "apply" */ "@/ticketing/views/ApplicationsClosed"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getEvent(routeTo.params.eventSlug)
          .then(res => {
            routeTo.params.event = res.data.event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/:id",
      name: "application-status",
      component: () =>
        import(/* webpackChunkName: "application-status" */ "@/ticketing/views/ApplicationStatus"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            TicketService.getEvent(routeTo.params.eventSlug),
            TicketService.getApplication(
              routeTo.params.eventSlug,
              routeTo.params.id
            )
          ])
          .then(
            axios.spread((eventRes, ticketOfferRes) => {
              const application = ticketOfferRes.data.application;
              const event = eventRes.data.event;
              store.dispatch("layouts/setLayout", event.layout);
              const ticketOffer = ticketOfferRes.data.ticketOffer;
              if (application.status === "pending") {
                routeTo.params.applicant = ticketOfferRes.data.applicant;
                routeTo.params.application = application;
                routeTo.params.ticketOffer = ticketOffer;
                routeTo.params.event = event;
                next();
              } else {
                next({
                  name: "ticket-offer-from-application",
                  params: {
                    eventSlug: routeTo.params.eventSlug,
                    id: ticketOffer.id
                  }
                });
              }
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/update-ticket-info/:id",
      name: "update-ticket-info",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketDetails"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(ticketOfferRes => {
            const ticketOffer = ticketOfferRes.data.ticketOffer;
            routeTo.params.ticketOffer = ticketOffer;
            const event = ticketOfferRes.data.event;
            store.dispatch("layouts/setLayout", event.layout);
            routeTo.params.event = event;

            ProfileService.getProfile(ticketOffer.personId)
              .then(profileRes => {
                routeTo.params.person = profileRes.data.person;
                next();
              })
              .catch(() => {
                next({ name: "404" });
              });
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/offer/spaces-full",
      name: "spaces-full",
      component: () =>
        import(/* webpackChunkName: "spaces-full" */ "@/ticketing/views/SpacesFull"),
      props: true
    },
    {
      path: "/:eventSlug/apply/offer/:id",
      name: "ticket-offer-from-application",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOfferFromApplication"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            TicketService.getEvent(routeTo.params.eventSlug),
            TicketService.getTicketOffer(
              routeTo.params.eventSlug,
              routeTo.params.id
            )
          ])
          .then(
            axios.spread((eventRes, ticketOfferRes) => {
              const ticketOffer = ticketOfferRes.data.ticketOffer;
              const event = eventRes.data.event;
              store.dispatch("layouts/setLayout", event.layout);
              const person = ticketOfferRes.data.recipient;
              if (event.soldOut) {
                next({
                  name: "spaces-full",
                  params: {
                    personName: person.firstName,
                    eventSlug: routeTo.params.eventSlug
                  }
                });
              } else if (ticketOffer.status === "void") {
                next({
                  name: "ticket-offer-from-application",
                  params: {
                    id: ticketOffer.newTicketOfferId,
                    eventSlug: routeTo.params.eventSlug
                  }
                });
              } else if (ticketOffer.status === "purchased") {
                next({
                  name: "ticket-offer-purchased",
                  params: {
                    id: routeTo.params.id,
                    eventSlug: routeTo.params.eventSlug
                  }
                });
              } else if (ticketOffer.status === "surrendered") {
                next({
                  name: "ticket-offer-surrendered",
                  params: {
                    id: routeTo.params.id,
                    eventSlug: routeTo.params.eventSlug
                  }
                });
              } else {
                routeTo.params.personInit = person;
                routeTo.params.ticketOffer = ticketOffer;
                routeTo.params.event = event;
                next();
              }
            })
          );
      }
    },
    {
      path: "/:eventSlug/apply/offer/:id/purchase",
      name: "ticket-offer-purchase",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOfferPurchase"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            TicketService.getEvent(routeTo.params.eventSlug),
            TicketService.getTicketOffer(
              routeTo.params.eventSlug,
              routeTo.params.id
            ),
            TicketService.getInvites(routeTo.params.id),
            TicketService.getNewInvite()
          ])
          .then(
            axios.spread(
              (eventRes, ticketOfferRes, invitesRes, newInviteRes) => {
                const ticketOffer = ticketOfferRes.data.ticketOffer;
                const event = eventRes.data.event;
                store.dispatch("layouts/setLayout", event.layout);
                const person = ticketOfferRes.data.recipient;
                const existingInvites = invitesRes.data.invites;
                const newInvite = newInviteRes.data.invite;
                if (event.soldOut) {
                  next({
                    name: "spaces-full",
                    params: {
                      personName: person.firstName,
                      eventSlug: routeTo.params.eventSlug
                    }
                  });
                } else if (ticketOffer.status === "void") {
                  next({
                    name: "ticket-offer-from-application",
                    params: {
                      id: ticketOffer.newTicketOfferId,
                      eventSlug: routeTo.params.eventSlug
                    }
                  });
                } else if (ticketOffer.status === "purchased") {
                  next({
                    name: "ticket-offer-purchased",
                    params: {
                      id: routeTo.params.id,
                      eventSlug: routeTo.params.eventSlug
                    }
                  });
                } else if (ticketOffer.status === "surrendered") {
                  next({
                    name: "ticket-offer-surrendered",
                    params: {
                      id: routeTo.params.id,
                      eventSlug: routeTo.params.eventSlug
                    }
                  });
                } else {
                  routeTo.params.person = person;
                  routeTo.params.ticketOffer = ticketOffer;
                  routeTo.params.event = event;
                  routeTo.params.existingInvites = existingInvites;
                  routeTo.params.newInvite = newInvite;
                  next();
                }
              }
            )
          );
      }
    },
    {
      path: "/:eventSlug/partners/registration/:ticketOfferId",
      name: "partner-registration",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/PartnerRegistration"),
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.ticketOfferId)
          .then(() => {
            store.dispatch(
              "people/getPerson",
              store.state.ticketOffers.ticketOffer.personId
            );
          });

        store
          .dispatch("events/getEvent", routeTo.params.eventSlug)
          .then(() => next());
      }
    },
    {
      path: "/apply/offer/:id/purchased",
      name: "ticket-offer-purchased",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-purchased" */ "@/ticketing/views/TicketOfferPurchased"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            store.dispatch("layouts/setLayout", response.data.event.layout);
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "purchased") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/apply/offer/:id/surrendered",
      name: "ticket-offer-surrendered",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-surrendered" */ "@/ticketing/views/TicketOfferSurrendered"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(routeTo.params.id)
          .then(response => {
            const ticketOffer = response.data.ticketOffer;
            if (ticketOffer.status === "surrendered") {
              routeTo.params.recipient = response.data.recipient;
              next();
            } else {
              next({
                name: "ticket-offer-from-application",
                params: { id: routeTo.params.id }
              });
            }
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound,
      props: true
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

export default router;
