import Vue from "vue";
import Vuex from "vuex";
import * as layouts from "@/store/modules/layouts.js";
import * as people from "@/store/modules/people.js";
import * as events from "@/store/modules/events.js";
import * as ticketOffers from "@/store/modules/ticketOffers.js";

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    layouts,
    people,
    events,
    ticketOffers
  },
  state: {
    loading: false,
    previousEvents: [
      "2009",
      "2010",
      "2012",
      "2013",
      "Youth 2013",
      "2014",
      "2015",
      "2016",
      "2017",
      "2018",
      "May 2019"
    ],
    industries: [
      "Art/Architecture/Design",
      "Automotive / Transportation",
      "Consumer Goods",
      "Education",
      "Finance/Investment/Banking",
      "Government",
      "Internet",
      "Law",
      "Media/Entertainment",
      "NGO/Nonprofit",
      "Professional Services",
      "Retail",
      "Science/Research/Medicine",
      "Technology/Electronics",
      "Telecommunications",
      "Venture Capital"
    ],
    educationLevels: [
      "Highschool",
      "Diploma/Certificate",
      "Bachelor's Degree",
      "Postgraduate Diploma/Certificate",
      "Master's Degree",
      "Doctorate"
    ],
    dietaryRequirements: ["Vegetarian", "Vegan", "Gluten Free", "Dairy Free"],
    accessabilityRequirements: ["Wheelchair", "Blind", "Deaf"],
    genders: ["Female", "Male", "Other"],
    birthYears: [...Array(1 + 2019 - 1900).keys()].map(v => 1900 + v).reverse(),
    livingSituations: ["Home Owner", "Renting", "Flatting", "Other"],
    satisfactionLevels: [1, 2, 3, 4, 5, 6, 7]
  },
  mutations: {
    setLoading(state, payload) {
      state.loading = payload;
    }
  },
  actions: {},
  getters: {
    previousEvents(state) {
      return state.previousEvents;
    },
    industries(state) {
      return state.industries;
    },
    educationLevels(state) {
      return state.educationLevels;
    },
    dietaryRequirements(state) {
      return state.dietaryRequirements;
    },
    accessabilityRequirements(state) {
      return state.accessabilityRequirements;
    },
    genders(state) {
      return state.genders;
    },
    birthYears(state) {
      return state.birthYears;
    },
    livingSituations(state) {
      return state.livingSituations;
    },
    satisfactionLevels(state) {
      return state.satisfactionLevels;
    }
  }
});
